import React from 'react';

import Card from '../../function/card';

import './index.css';

const Portfolio = (props) => {
	const _handleProject = () => {
		window.open('https://deepbig.github.io', '_blank');
	}
	const array = [
		{
			isPersonal: 1,
			isProgress: 0,
			title: '#Camagru',
			descriptionEN: 'This project is a Social Network Service(SNS) for sharing pictures like Instagram. It is also a responsive Single Page Application(SPA). The front end was developed using React and Redux, and the back end was developed using Node js. A key feature of the project is the ability to take pictures using cameras, upload local images, and apply filters or stickers to the shared images.',
			descriptionKR: '이 프로젝트는 인스타그램처럼 사진 공유 소셜 네트워크 서비스입니다. 이 또한 반응형 단일 페이지 어플리케이션입니다. 프론트엔드는 React와 Redux를 활용하였으며, 백엔드는 Java 프레임워크인 Springboot로 개발하였습니다. 이 프로젝트의 핵심기능은 카메라를 활용하거나 로컬의 사진을 업로드하여 필터 혹은 스티커를 적용하고 공유하는 것입니다.',
			language: ['JAVASCRIPT', 'REACT', 'REDUX', 'MYSQL', 'HTML5', 'CSS3'],
			docs: 'https://github.com/deepbig/camagru/',
			code: 'https://github.com/deepbig/camagru/',
			demo: 'https://deepbig.github.io/camaguru',
			lang: props.lang
		},
		{
			isPersonal: 1,
			isProgress: 0,
			title: 'Jamming Spotify',
			descriptionEN: "Jamming Spotify is a simple React App for adding custom playlists to the user's Spotify account. I combined multiple components, interactive search functionality, and requests to the Spotify API.",
			descriptionKR: '이 프로젝트는 인스타그램처럼 사진 공유 소셜 네트워크 서비스입니다. 이 또한 반응형 단일 페이지 어플리케이션입니다. 프론트엔드는 React와 Redux를 활용하였으며, 백엔드는 Java 프레임워크인 Springboot로 개발하였습니다. 이 프로젝트의 핵심기능은 카메라를 활용하거나 로컬의 사진을 업로드하여 필터 혹은 스티커를 적용하고 공유하는 것입니다.',
			language: ['JAVASCRIPT', 'REACT', 'SpotifyAPI', 'HTML5', 'CSS3'],
			docs: '',
			code: '',
			demo: 'http://jamming-spotify.surge.sh/',
			lang: props.lang
		},
		{
			isPersonal: 0,
			isProgress: 1,
			title: 'M@TCH@',
			descriptionEN: 'M@TCH@ is a group project that is currently work in progress. We are using React for front-end development, and Express for the back-end. This is a dating app like Tinder which matches users who are looking for a partner. The key functions of this app is location-based recommandation and chat feature by utilizing sign-in features through social networks and Google Maps APIs.',
			descriptionKR: '이 프로젝트는 현재 진행중인 그룹 프로젝트입니다. 역시 프론트엔드 개발은 React로 진행하고 있으며, 백엔드는 Express로 개발하고 있습니다. Tinder와 같이 데이팅 상대를 위치기반으로 매칭하고 채팅 기능을 제공하는 어플리케이션이 될 것입니다. 또한, 소셜네트워크를 활용한 로그인 기능과 구글 맵 API를 적극 활용 중에 있습니다.',
			language: ['JAVASCRIPT', 'REACT', 'REDUX', 'NODE', 'EXPRESS', 'MYSQL', 'HTML5', 'CSS3'],
			docs: '',
			code: '',
			demo: '',
			lang: props.lang
		},
	]
	return (
		<div className='portfolio'>
			{!props.lang 
				? 
				<h1>What Projects Have I Done?</h1>
				: 
				<h1>최근 프로젝트</h1>
			}
			{!props.lang 
				? 
				<h2>Here are some recent projects. You can check out all about these projects on my GitHub.</h2>
				: 
				<h2>저의 최근 프로젝트들 입니다. 제 깃허브 페이지에서 모든 프로젝트 정보를 확인하실 수 있습니다.</h2>
			}
			<div>
				{array.map((data, index) => 
					<Card data={data} key={index} />
				)}
			</div>
			<h2>Do you want to see more projects that I made?</h2>
			<div className='about-button' onClick={ () => _handleProject() }>Click Me</div>
		</div>
	);
}

export default Portfolio;