import React from 'react';

import { FaMapMarkerAlt, FaPhoneSquare } from 'react-icons/fa';
import { IoMdMail } from 'react-icons/io';

import './index.css';

const About = (props) => {

	const _handleHyperlinkToCollege = () => {
		window.open('https://www.bryant.edu', '_blank');
	}

	const _handleHyperlinkToBootcamp = () => {
		window.open('https://www.42.us.org', '_blank');
	}

	const _handleDetail = () => {
		document.getElementById('alert').style.top = '0rem';
		setTimeout(() => {
			document.getElementById('alert').style.top = 'calc(0% - 3rem)';
		}, 2500);
	}

	return (
		<div className='about'>
			<div className='about-detail'>
				I am a highly motivated and determined recent graduate of <font className='about-highlight' onClick={_handleHyperlinkToCollege}>#BryantUniversity</font>. I am currently studying software engineering at <font className='about-highlight' onClick={_handleHyperlinkToBootcamp}>#42SiliconValley.</font><br />
			</div>
			<div className='about-info'>
				<FaMapMarkerAlt className='about-info-icon' />
				<div className='about-info-detail'>Fremont, CA, USA</div>
				<IoMdMail className='about-info-icon' />
				<div className='about-info-detail'>hryu@grad.bryant.edu</div>
				<FaPhoneSquare className='about-info-icon' />
				<div className='about-info-detail'>+1 (401) 234 - 3483</div>
			</div> 

		</div>
	);
}

export default About;