import React from 'react';

import { IoMdMail, IoLogoGithub, IoLogoLinkedin } from 'react-icons/io';

import TextLoop from "react-text-loop";


import './index.css';

const Greeting = (props) => {

	const _handleLinkToGithub = () => {
		window.open('https://github.com/deepbig/', '_blank');
	}

	const _handleLinkToLinkedin = () => {
		window.open('https://www.linkedin.com/in/hongsuk/', '_blank');
	}

	return (
		<div className='greeting'>
			<div className='greeting-container'>
				<IoLogoLinkedin className='greeting-link-icon' onClick={_handleLinkToLinkedin} />
				<IoLogoGithub className='greeting-link-icon' onClick={_handleLinkToGithub} />
				<a href='mailto:hryu@grad.bryant.edu'><IoMdMail className='greeting-link-icon'/></a>
			</div>
			<h1>Hi <span className="wave">👋</span></h1>
			<h2>I'm Hongsuk.</h2>
			<h3>I'm <TextLoop interval={2500} springConfig={{ stiffness: 180, damping: 8 }} children={[" a Full-Stack Web Developer", " an International Entrepreneur", " a Great Team Player", " a Happy Learner", " a Challenge Lover", " Perseverant" , " Humble"]}></TextLoop> !</h3>
		</div>
	);
}

export default Greeting;