import React from 'react';

import Picture1 from '../../../resources/profile.jpeg';
import Picture2 from '../../../resources/Ryu_resized1.jpg';
import Picture3 from '../../../resources/Professional_Picture_resized1.jpg';

import './index.css';

const Profile = () => {
	return (
		<div className='profile'>
			<div className='profile-bg'></div>
			<div className='profile-picture1' style={{ backgroundImage: 'url(\'' + Picture1 + '\')' }}>
				<div className='profile-picture2' style={{ backgroundImage: 'url(\'' + Picture2 + '\')' }}></div>
				<div className='profile-picture3' style={{ backgroundImage: 'url(\'' + Picture3 + '\')' }}></div>
				<div className="penguin">
            <div className="penguin-bottom">
              <div className="right-hand"></div>
              <div className="left-hand"></div>
              <div className="right-feet"></div>
              <div className="left-feet"></div>
            </div>
            <div className="penguin-top">
              <div className="right-cheek"></div>
              <div className="left-cheek"></div>
              <div className="belly"></div>
              <div className="right-eye">
                <div className="sparkle"></div>
              </div>
              <div className="left-eye">
                <div className="sparkle"></div>
              </div>
              <div className="blush-right"></div>
              <div className="blush-left"></div>
              <div className="beak-top"></div>
              <div className="beak-bottom"></div>
            </div>
          </div>			
			</div>


		</div>
	);
}

export default Profile;